.shimmer-loading {
    background: linear-gradient(to right,#00BFFF, #f07816, #00BFFF,);
    background-size: 1200px 104px;
    position: relative;
    animation: loading 2s linear infinite;
  }
  
  @keyframes loading {
    0% {
      background-position: 0px 0;
    }
    50% {
      background-position: 1200px 0;
    }
    100% {
      background-position: 0px 0;
    }
  }